.footer{
    display: flex;
    font-size: 15px;
    background: rgb(19, 19, 83);
    color: #fff;
    gap: 45px;
    align-items: center;
    justify-content: center;
}
.pg p{
  margin-top: 3rem;
}
.abo h1{
  margin-top: 3rem;
}
.lat h1{
  margin-top: 3rem;
}

.column-icon i{
    color: lightblue;
    padding: 8px;
    padding-left: 0px;
    font-size: 30px;
  }
  .column-icon i:hover{
    transform: scale(1.5);
    color: red;
  }
.num h3{
    margin-top: 2rem;
}

@media (min-width: 576px) {
    .footer{
        display: flexbox;
        flex-direction: row;
      grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
    }
    .footer1 {
      padding-bottom: 0.25rem;
    }
}