.Project{
    text-align: center;
    justify-content: center;
    margin-bottom: 2rem;
    color: white;
}
.Project h1{
    margin-top: 4rem;
    font: large;
}
.Project p{
    font-size: 15px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    letter-spacing: 0.2px;
    margin-bottom: 2rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.Project button{
    padding: 1rem 4rem;
    margin-top: 1rem;
    border-radius: 1rem;
}
.btn2{
    border: 1px solid red;
    background: transparent;
    padding: 4px 10px;
    font-size: 1.5rem;
    color: red;
    background: lightblue;
    margin-bottom: 4rem;
    border-radius: 50%;
    margin:15px
}
.btn2:hover{
    color: white;
    background: red;
}
.values{
    background-color: rgb(20, 20, 99);
    text-align: center;
    justify-content: center;
}
.values .ace{
    opacity: 2rem;
    color: thistle;
    margin-bottom: 5rem;
}
