*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.about{
   
    width: 60%;
    max-width: 60%;
    height: 100%;
    margin: 5rem auto;
    display: grid;
    gap: 4rem;
}
@media screen and (min-width:768px) {
    .about{
        grid-template-columns: repeat(3,1fr);
    }    
}
.aboutus{
    border-radius: 1rem;
    padding: 1rem 2rem;
}
.aboutus h2{
    color: pink;
    margin-top: 1rem;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}
.aboutus p{
    color: whitesmoke;
    margin-top: 2rem;
    margin-bottom: 1rem;
    font-size: 17px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.aboutus button{
    font-size: 15px;
    margin-top: 0.5rem;
    margin-bottom: 3rem;
    border-radius: 1rem;
    padding: 1rem 1rem;
}
.aboutus img{
    width: 20%;
    height: 5%;
}