.choose{
    position: relative;
    height: 100vh;
}
.contain{
    align-items: center;
    justify-content: center;
    position: relative;
    height: 90vh;
    width: 100%;
}
@media screen and (min-width:768px) {
    .choose{
        grid-template-columns: repeat(3,1fr);
        display: grid;
    }    
}