.navbar{
    height: 10vh;
    background-image: url("data:image/jpeg;base64,");
    display: flex;
    justify-content: center;
    align-items: center;
}
.navbar-logo{
    color: white;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 10rem;
}
.nav-items{
    display: flex;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: flex-end;
    margin-right: 4rem;
}
.nav-items{
    display: flex;
    align-items: center;
    height: 15vh;
}
.nav-items a {
    text-decoration: none;
    color: white;
    font-size: 1.5rem;
    margin-right: 2rem;
    padding: 6px 16px;
    border-radius: 5px;
}
.nav-items a:hover {
    background: red;
}