.services-submenu{
    width: 10rem;
    position: absolute;
    list-style: none;
    text-align: start;
    top: 80px;
}
.services-submenu li{
    background: white;
    cursor: pointer;
}
.submenu-item{
    display: block;
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: black;
    padding: 16px;
}
.submenu-item:hover{
    background: red;
}
.services-submenu-clicked{
    display: none;
}

